$(function() {
    const $tableNav = $('.js-table-nav');
    const $tableSlider = $('.js-table-slider');
    const $tableParamsSlider = $('.js-params-slider');

    const navEvent = e => {
        const { dir } = e.currentTarget.dataset
        if (dir === 'prev') $tableSlider.slick('slickPrev')
        if (dir === 'next') $tableSlider.slick('slickNext')
    }

    const responsive = [
        {
            breakpoint: 1440,
            settings: {
                slidesToShow: 4.5,
                slidesToScroll: 1,
                dots: false,
                arrows: false,
            }
        },
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 3.5,
                slidesToScroll: 1,
                dots: false,
                arrows: false,
            }
        },
        {
            breakpoint: 660,
            settings: {
                slidesToShow: 2.5,
                slidesToScroll: 1,
                dots: false,
                arrows: false,
            }
        },
        {
            breakpoint: 460,
            settings: {
                slidesToShow: 1.5,
                slidesToScroll: 1,
                dots: false,
                arrows: false,
            }
        },
    ]

    $tableSlider.on('destroyTableSlider', ()=> {
        $tableSlider.slick('unslick')
        $tableParamsSlider.slick('unslick')
        $tableNav.off('click', 'button', navEvent)
    })

    $tableSlider.on('initTableSlider', ()=> {
        $tableNav.on('click', 'button', navEvent)

        $tableSlider.slick({
            infinite: false,
            slidesToShow: 5.6,
            slidesToScroll: 1,
            asNavFor: '.js-params-slider',
            arrows: false,
            dots: false,
            responsive,
        });

        $tableParamsSlider.slick({
            infinite: false,
            slidesToShow: 5.6,
            slidesToScroll: 1,
            asNavFor: '.js-table-slider',
            arrows: false,
            dots: false,
            draggable: false,
            touchMove: false,
            swipe: false,

            responsive,
        })
    })
})