$(()=> {
    const $config = $('.js-modal-config');
    
    if ($config.length) {
        $config.on('click', '[data-tab]', function() {
            console.log(this)
            const $that = $(this);
            const tab = $that.data('tab');
            $that.addClass('modal__tabs-btn_active');
            $that.parent().siblings().children().removeClass('modal__tabs-btn_active')
            $that.parents('.js-modal-config').find(`[data-content="${tab}"]`).addClass('is-active').siblings().removeClass('is-active')
        })
    }
})