

$(function () {
     
    const $map = $(".js-contacts-map");

    const addresses = [
        {
            address: "Подольск, пр-т Юных Ленинцев, <br> дом 1И, стр. 2",
            coord: [55.468045, 37.559236], 
            tel: "+7 (495) 492-09-45",
        },
        {
            address: "Москва, Ярославское ш., <br> владение 2В, строение 3 (95 км МКАД)",
            coord: [55.846295, 37.675325], 
            tel: "+7 (495) 432-59-97",
        }
    ]

    const contentTemplate = ({ address, tel }) => {
        return /*html*/`
            <div class="balloon-content">
                <div class="balloon-content__address">
                    <span>${address}</span>
                </div>

                <a href="tel:+7(495)432-59-97" class="balloon-content__tel">${tel}</a>
            </div>
        `
    }


     
    if (!$map.length) return;

    ymaps.ready(init)

    function init() {
        const MAP = new ymaps.Map("ymap", {
            center: [55.468045, 37.559236],
            zoom: 9,
            controls: []
        })

        addresses.forEach(address => {
            const PIN = new ymaps.Placemark(address.coord, {
                hintContent: address.name,
                balloonContent: contentTemplate(address),
                iconContent: '12'
            }, {
                iconLayout: 'default#image',
                iconImageHref: './img/content/map-pin.png',
                iconImageSize: [28, 54],
                iconImageOffset: [0, 0]
            })

            MAP.geoObjects.add(PIN)
        })
    }

})