$(function () {
    const $footerAccordion = $(".js-footer-accordion");

    if (!$footerAccordion.length) return;

    $footerAccordion.on("click", function () {
        const that = $(this);
        const nextEl = that.next();
        const nextHeight = nextEl[0].scrollHeight
        that.toggleClass("footer__top_active")

        if (that.hasClass("footer__top_active")) {
            return nextEl.css({
                "max-height": nextHeight,
                overflow: "visible",
                opacity: 1,
            })
        }

        nextEl.css({
            "max-height": 0,
            overflow: "hidden",
            opacity: 0,
        })
    })
})