$(function(){
    const $promotionsSlider = $('.js-promotions-slider');
    const $promotionsPrev = $('.js-slider-prev');
    const $promotionsNext = $('.js-slider-next');

    if(!$promotionsSlider.length) return;

    $promotionsSlider.slick({
        slidesToShow: 3,
        slidesToScroll: 1,
        dots: false,
        arrows: false,
        // centerMode: true,
        responsive: [
            {
                breakpoint: 1461,
                settings: {
                    slidesToShow: 2.5,
                    slidesToScroll: 1,
                    dots: false,
                    arrows: false,
                }
            },

            {
                breakpoint: 1241,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1,
                    dots: false,
                    arrows: false,
                }
            },

            {
                breakpoint: 1025,
                settings: {
                    slidesToShow: 1.1,
                    slidesToScroll: 1,
                    dots: false,
                    arrows: false,
                    adaptiveHeight: true,
                }
            },
        
        ]
    });

    $promotionsPrev.on('click', ()=> $promotionsSlider.slick('slickPrev'));
    $promotionsNext.on('click', ()=> $promotionsSlider.slick('slickNext'));
});