$(function () {
    const $accordion = $(".js-modal-accordion");
    const $body = $('body')

    if (!$accordion.length) return;

    $body.on("click", '.js-modal-accordion', function () {
        const that = $(this);
        const nextEl = that.next();
        const nextHeight = nextEl[0].scrollHeight
        that.toggleClass("accordion__head_active")

        if (that.hasClass("accordion__head_active")) {
            return nextEl.css({
                "max-height": nextHeight,
                overflow: "visible",
                opacity: 1,
            })
        }

        nextEl.css({
            "max-height": 0,
            overflow: "hidden",
            opacity: 0,
        })
    })
})